.clearfix::after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: '';
  clear: both;
  height: 0;
}
* {
  margin: 0;
  box-sizing: border-box;
  font-family: 'Roboto', 'Open Sans';
  outline: 0;
}
html,
body,
.wrapper {
  height: 100%;
}
body {
  background: #14223e;
}
.wrapper {
  padding: 3%;
  position: relative;
  background-size: cover;
  color: #fff;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 100;
}
.left-side {
  position: relative;
  z-index: 10;
  width: 15%;
  max-width: 250px;
  float: left;
  opacity: 0;
  transition: 0.8s linear 0.2s;
  transform-origin: left top;
}
.left-side > * {
  white-space: nowrap;
  backface-visibility: hidden;
}
.panel-1 {
  margin-bottom: 10px;
}
.panel-2 {
  margin-bottom: 23px;
}
.panel-text {
  display: inline-block;
  vertical-align: middle;
  color: #fff;
  font-size: 18px;
  line-height: 24px;
  margin-left: 20px;
}
.arrow-to-main {
  position: absolute;
  left: -14%;
  top: 15.5%;
  font-size: 21px;
  line-height: 21px;
  color: #fff;
  text-decoration: none;
  transition: 0.3s;
  backface-visibility: hidden;
}
.arrow-to-main svg {
  transform-origin: center;
  transform: rotate(180deg);
}
.arrow-to-main span {
  opacity: 0;
  position: absolute;
  font-size: 14px;
  bottom: 40px;
  transition: 0.3s;
}
.arrow-to-main:hover {
  transform: scale(1.2);
  cursor: pointer;
  backface-visibility: hidden;
}
.arrow-to-main:hover span {
  opacity: 1;
}
.sandwich-wrap {
  width: 80px;
  display: inline-block;
  vertical-align: middle;
}
.menu-sandwich {
  display: block;
  margin: auto;
  position: relative;
  width: 54px;
  height: 54px;
  border: 2px solid #fff;
  border-radius: 50%;
  text-align: center;
  font-size: 0;
  transition: 0.4s;
}
.menu-sandwich::after {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}
.menu-sandwich:hover {
  cursor: pointer;
  background: #fff;
}
.menu-sandwich:hover .sandwich-item {
  background: #008000;
}
.menu-sandwich.rotate {
  transform: rotate(180deg);
}
.menu-sandwich.rotate .sandwich-item {
  transform-origin: center;
  width: 24px;
  margin: 0;
  backface-visibility: hidden;
}
.menu-sandwich.rotate .sandwich-item:nth-child(1) {
  transform: rotate(-45deg) translate(-2px, 1px);
}
.menu-sandwich.rotate .sandwich-item:nth-child(2) {
  opacity: 0;
}
.menu-sandwich.rotate .sandwich-item:nth-child(3) {
  transform: rotate(45deg) translate(-2px, -1px);
}
.sandwich-items {
  display: inline-block;
  margin: -2px 0;
  vertical-align: middle;
}
.sandwich-item {
  width: 21px;
  height: 2px;
  margin: 4px 0;
  background: #fff;
  transition: 0.4s;
  backface-visibility: hidden;
}
.logo {
  display: inline-block;
  vertical-align: middle;
  transition: 0.4s linear 0.2s;
  transform-origin: center;
}
.logo:hover {
  transform: scale(1.1);
}
.logo img {
  backface-visibility: hidden;
}
.menu-apartment {
  display: inline-block;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: rgba(70,86,95,0.51);
  text-align: center;
  font-size: 0;
}
.menu-apartment::after {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}
.menu-apart-white {
  display: inline-block;
  vertical-align: middle;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #fff;
}
.menu-apart-white::after {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}
.menu-apart-white:hover {
  cursor: pointer;
}
.menu-apart-white:hover .menu-apart-item:nth-child(1)::after {
  animation: point-1 1.4s linear 0s infinite;
}
.menu-apart-white:hover .menu-apart-item:nth-child(2)::after {
  animation: point-2 2s linear 0s infinite;
}
.menu-apart-white:hover .menu-apart-item:nth-child(3)::after {
  animation: point-1 1.8s linear 0s infinite;
}
.menu-apart-items {
  display: inline-block;
  vertical-align: middle;
}
.menu-apart-item {
  position: relative;
  width: 23px;
  height: 2px;
  margin: 6px 0;
  background: #008000;
  transition: 0.4s;
}
.menu-apart-item::after {
  transform-origin: center;
  box-sizing: border-box;
  content: '';
  position: absolute;
  top: -3px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  border: 2px solid #008000;
  background: #fff;
}
.menu-apart-item:nth-child(1)::after {
  right: 20%;
}
.menu-apart-item:nth-child(2)::after {
  right: 40%;
}
.menu-apart-item:nth-child(3)::after {
  right: 20%;
}
.cam-wrap {
